html {
    box-sizing: border-box;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}



body {
    margin: 0;
    font-family: "Lato", sans-serif;
    background-color: #fff;
    font-size: 16px;
    color: $fontl;
    line-height: 1.4;
    overflow-x: hidden;
}

p {
    color: $fontl;
    font-size: 16px;
    padding: 7px 0;

}

img {
    max-width: 100%;
    height: auto;
}

a {
    text-decoration: none;
    transition: all .3s;
}

.block-title {
    font-family: "Forum", sans-serif;
    font-size: 54px;
    margin-bottom: 20px;
    color: $fontd;
    line-height: 1.3;
}

.content {
    background: #fff;
    box-shadow: 5px 5px 20px #6363631c;
    padding: 15px;
    margin-top: -60px;
}


@media only screen and (max-width : 1200px) {
    .content-planes {

        margin-top: 0px;
    }

}

@media only screen and (max-width : 504px) {
    .block-title {
        font-size: 28px;
        margin-bottom: 10px;
    }
}

@media only screen and (max-width : 980px) {
    .order2 {
        order: 1;
    }

    .order1 {
        order: 2;
    }
}
